	import { BASE_URL } from '@/api'
	export default {
		name: '',
		props: {
			all_demand_type: Array
		},
		data () {
			return {
				identity: ['我是需求方', '我是服务商'],
				// typeList: [
				// 	{icon: '/static/images/g5@2x.png', name: '设计类目'}, 
				// 	{icon: '/static/images/g2@2x.png', name: '制作安装'},
