		onLoad () {

		},
		computed: {
			BASE_URL () {
				return BASE_URL
			},
			typeList () {
				let arr = this.all_demand_type.slice(0, 4)
				return arr
			}
 		},
		methods: {
			onIdentityClick (index) {
				if(this.current !== index) {
					this.current = index;
